
import { defineComponent } from '@vue/composition-api';
import { MaterialGoods } from '@/models/MaterialGood';
import CommodityItem from './CommodityItem.vue';

export default defineComponent({
  props: {
    materialList: {
      type: Array as () => MaterialGoods[],
      required: true,
    },
    displayMode: {
      type: String as () => 'drape' | 'flat',
      default: 'drape',
    },
  },
  inject: ['isQt'],
  render() {
    return <ul class="flex-wrap records-list">
      {
        this.materialList.map((material, idx) => <li key={material.id} class='commodity-item'>
          <CommodityItem materialInfo={material} displayMode={this.displayMode}
            data-img-type='material-item' onpreview={() => this.preview(idx)}></CommodityItem>
        </li>)
      }
    </ul>;
  },
  setup(props, { root }) {
    async function preview(index: number) {
      let srcList;
      if (props.displayMode === 'flat') {
        srcList = props.materialList.map((material) => material.imgJson?.texture || material.imgJson?.flat);
      } else {
        srcList = props.materialList.map((material) => material.imgJson.thumbnail);
      }
      // @ts-ignore
      await root.$ImageViewer.show(srcList, {
        index,
        targetElems: '[data-img-type=\'material-item\']',
      });
    }

    return {
      preview,
    };
  },
});
